@import "core/settings";

body {
  background-color: lightseagreen;
}

.home {
  margin: 20px;
}

.page__header {
  height: 75px;
  width: 100%;
}

.logos_container {
  position: relative;
}

.logo_1 {
  position: absolute;
  top: 10px;
  left: 10px;
}

.logo_2 {
  position: absolute;
  top: 10px;
  right: 20px;
}

.section1 {
  height: 50vh;
  border: solid blue 2px;
  background-color: lightskyblue;
  text-align: center;
  overflow: scroll;
  padding: 15px;
}

.button {
  border: 5px solid red;
  border-radius: 15px;
  height: 45px;
  width: 300px;
  background-color: darkred;
  display: flex;
  align-items: center;
  justify-content: center;
}

.wizarding_world_a {
  text-decoration: none;
  color: salmon;
  font-family: "Poor Story", cursive;
  font-weight: bold;
  font-size: 18px;
}

@media (min-width: 768px) {
  /**tablet**/
  .page__header {
    height: 100px;
  }
}

@media (min-width: 1280px) {
  /**desktop**/
  .page__header {
    height: 110px;
  }
}
